.header {
  background: #202020;
  width: 100%;
  height: 70px;
  padding: 0 1.5rem;
  transition: 0.3s ease all;
}
@media (min-width: 48em) {
  .header {
    padding: 0 3rem;
 }
}
.header__content {
  overflow: hidden;
  color: #fff;
  margin: 0 auto;
  max-width: 1920px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  z-index: 100;
}
.header__content__logo {
  font-size: 2rem;
  font-weight: bold;
  text-decoration: none;
  color: #fff;
}
.header__content__nav {
  top: 0;
  right: 100%;
  bottom: 0;
  width: 100%;
  height: 100vh;
  position: fixed;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  background: rgba(32, 32, 32, 0.9);
  -webkit-backdrop-filter: blur(2px);
          backdrop-filter: blur(2px);
  -webkit-transform: translate(0);
          transform: translate(0);
  transition: 0.3s ease transform;
}
@media (min-width: 48em) {
  .header__content__nav {
    -webkit-transform: none;
            transform: none;
    flex-direction: row;
    background: transparent;
    width: auto;
    height: 100%;
    position: static;
 }
}
.header__content__nav ul {
  list-style: none;
  padding: 0;
  display: flex;
  flex-direction: column;
  margin-bottom: 32px;
}
@media (min-width: 48em) {
  .header__content__nav ul {
    flex-direction: row;
    align-items: center;
    margin-bottom: 0;
    margin-right: calc(0.5rem + 16px);
 }
}
.header__content__nav ul li:not(:last-child) {
  margin-bottom: 32px;
}
@media (min-width: 48em) {
  .header__content__nav ul li:not(:last-child) {
    margin-bottom: 0;
    margin-right: 16px;
 }
}
.header__content__nav ul li a {
  text-decoration: none;
  color: inherit;
  padding: 0.75rem 1.25rem;
  border-radius: 12px;
  transition: 0.3s ease all;
}
.header__content__nav ul li a:hover {
  background: rgba(255, 255, 255, 0.1);
}
.header__content__nav ul li a:active {
  border-radius: calc(12px + 6px);
  background: linear-gradient(rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.2));
}
.header__content__nav .btn {
  cursor: pointer;
  outline: none;
  padding: 0.75rem 1.25rem;
  border-radius: 12px;
  font-size: 1rem;
  font-family: inherit;
  background: #a2a2f6;
  color: #202020;
  border: 1px solid transparent;
  transition: 0.3s ease all;
  margin: 0.5em 0.5em;
}
.header__content__nav .btn:hover {
  border-color: #a2a2f6;
  background: rgba(162, 162, 246, 0.1);
  color: #a2a2f6;
}
.header__content__nav .btn:active {
  border-color: #a2a2f6;
  background: linear-gradient(rgba(162, 162, 246, 0.2), rgba(162, 162, 246, 0.3));
  color: #a2a2f6;
}
.header__content__nav .btn__login {
  background-color: #202020;
  color: #fff;
}
.header__content__nav.isMenu {
  -webkit-transform: translate(100%);
          transform: translate(100%);
}
.header__content__toggle {
  cursor: pointer;
  display: flex;
  align-items: center;
  font-size: 2rem;
  transition: 0.3s ease all;
  position: relative;
}
.header__content__toggle:hover {
  color: #a2a2f6;
}
@media (min-width: 48em) {
  .header__content__toggle {
    display: none;
 }
}


